* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body, html {
    height: 100%;
    width: 100%;
}

svg {
    width: 100%;
    height: 100%;
}

polygon {
    stroke: red;
    stroke-width: 1;
}

#world {
    width: 100vw;
    height: 100vh;
    overflow-y: hidden;
}

.wallpaper {
    background-repeat: repeat-x;
    background-size: cover;
}

.menu {
    position: absolute;
    top: 0;
}